import React from "react";

import "./Summary.scss";
import { TextAreaField, BlockTitle } from "..";
// import classNames from 'classnames'

const Summary = () => {
  return (
    <div className="Summary">
      <BlockTitle content="" />
      <p className="text-md">
         2-4    ,  
        !   ,     -   
        ,    .
      </p>
      <TextAreaField
        placeholder=" "
        singleItem="summary"
        rows="5"
      />
    </div>
  );
};

export default Summary;
